*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* navbar */
.containernav{
  width: 100%;
  background-color: rgb(12, 168, 12);
}
.nav90{
  height: 28px;
 padding-top: 3px;
 width: 90%;
 margin: auto;
  
}
.nav90 h1{
  font-size: 16px;
  color: rgb(249, 249, 250);
  display: inline;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.nav90 ul{
  float: right;
}
.nav90 ul li{
  list-style: none;
  display: inline;
  margin-right: 15px;
}
.nav90 ul li:hover{
  background-color: whitesmoke;
  color: rgb(17, 175, 122);
}

.navbtn{
  float: right;
  display: none;
  background-color: rgb(237, 237, 243);
  height: 21px;
  width: 21px;
  font-size: 10px;
  border: none;
  font-weight: bold;
  margin-right: 3px;
  border-radius: 3px;
}
.cartbtn{
  float: right;
  padding: 3px;
  
  height: 21px;
  background-color: rgb(240, 22, 167);
  color: rgb(248, 248, 244);
  border: none;
  border-radius: 3px;
}

.navlink{
  text-decoration: none;
  font-size: 14px;
  color:whitesmoke;
}
.navlink:hover{
  color: rgb(20, 167, 20);
}
@media (max-width:650px) {
  .navbtn{
    display: block;
  }
  .nav90 ul{
    display: none;
    background-color: rgb(47, 240, 56);
    width: 100%;
  }
  .nav90 ul li{
    display: block;
    width: 100%;
    text-align: center;
  }
.navlink{
  color: rgb(247, 244, 247);
  width: 100%;

}
  .navlink:hover{
    width: 100%;
    color: rgb(241, 31, 213);
  }
 

}
.mardiv{
  width: 90%;
  margin: auto;
}

.mardiv img{
  width: 60%;
  height: 200px;
  border-radius: 5px;
}
/* cartpage */
.cartpage{
  background-color: rgb(131, 177, 247);
  min-height: 80vh;
}

/* banner  */
.mainbanner{
  width: 100%;
  background-color: greenyellow;
  /* background-image: url('./images/backgrdmobils.jpg'); */
  background-size: cover;
  
}
.subbanner{
  width: 90%;
  min-height: 80vh;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

}

.bannerlist{
  width: 40%;
  /* background-color: rgb(204, 241, 67); */
  padding: 5px 2px 3px 2px;
 
}
.bannerlist h4{
  color: rgb(228, 46, 188);
}
.bannerlist p{
  font-size: 10px;
  color: rgb(128, 20, 74);
  padding: 5px 0;
}

.bannerlist ul li{
  color: rgb(223, 39, 78);
  padding: 3px;
  font-size: 10px;
  list-style: none;
  margin-top: 7px;
  border-bottom: 1px solid gray;
  transition: 0.5s;
  
}
.bannerlist ul li:hover{
  background-color: rgb(175, 25, 175);
  color: whitesmoke;
}
.bannerimg{
  display: flex;
  justify-content: center;
  width: 40%;
  /* background-image: url('./images/manrepair.jpg');
  background-size: cover; */
  /* animation-name: iimg;
  animation-iteration-count: infinite;
  animation-duration: 2s; */
}
.bannerimg img{
  width: 100%;
  height: 200px;
  border-radius: 3px;
}
.navmar{
  width: 100%;
  background-color:whitesmoke;
  color: rgb(236, 41, 194);
}
/* body section */
.containerbody{
  width: 100%;
  
}
.containerbody90{
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-auto-rows: auto;
  grid-column-gap: 25px;
  grid-row-gap: 15px;
}

.bodycard{
  /* width: 150px; */
  /* flex:1; */
  border: 0.2px solid violet;
  border-radius: 3px;
  box-shadow: 3px;
  text-align: center;
}
.bodycard img{
  width: 100%;
  height: 150px;
  border-radius: 3px;
  
}
.bodycard p{
  font-size: 8px;
  color: gray;
}
.bodycard h5{
  color: rgb(18, 160, 18);
}
.bodycard button{
  border: none;
  background-color: rgb(245, 38, 166);
  color: whitesmoke;
  border-radius: 2px;
  padding: 3px;
  font-size: 10px;
  margin-right: 3px;
}
.bodycard button:hover{
  background-color: rgb(15, 199, 15);
  
}
.cart{
  background-color: rgb(222, 248, 237);
  border-radius: 10px;
  padding: 5px;
}
@media (max-width:650px) {
  .containerbody90{
    grid-template-columns: repeat(2.2fr);
  }
  .bannerlist{
    width: 100%;
  }
  .bodycard img{
    width: 90%;
    height: 100px;
    
    
  }
  .subbanner{
    flex-direction: column;
  }
  .bannerimg {
    width: 100%;
    padding: 10px;
  }
}

@media (max-width:390px) {
  .containerbody90{
    grid-template-columns: repeat(1,1fr);
  }
}
.order{
  background-color: rgb(119, 240, 209);
  min-height: 100vh;
}

/* service */
.service{
  background-color: rgb(48, 233, 48);
  /* display: flex;
  align-items: center;
  justify-content: center; */
  height: 100vh;
}
.service img{
  width: 100%;
  height: 80vh;
  padding: 10px;

}
.footer100{
  margin-top: 10px;
  width: 100%;
  min-height:40vh ;
  background-color: rgb(17, 219, 44);
  /* border-bottom: 0.5px solid rgb(37, 236, 143); */
}
.footer90{
  width: 90%;
  margin: auto;
  display: flex;
}
.footer1{
  flex: 1;
  /* background-color: rgb(238, 30, 151); */
  color: rgb(43, 10, 29);
  padding: 10px;
}
.footer90 h3{
  color: rgb(240, 29, 159);
  
}
.buybtn{
  background-color: green;
}
.footer90 p{
  font-size: 10px;
}
.footer2 {
  text-align: center;
  flex: 1;
  /* height: 400px; */
  color: rgb(250, 245, 250);
  padding: 10px;
}
.cartnum{
  background-color: whitesmoke;
  border-radius: 50%;
  padding: 2px;
  color: rgb(216, 6, 163);
  font-size: 7px;
  margin-bottom: 2px;

}
@media (max-width:650px) {
  .footer90{
    flex-direction: column;
  }
  .footer2{
    background-color: rgb(45, 223, 45);
  }
}
